@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.passive-product-detail-item {
  width: 32.6%;
  float: left;
  margin-right: 1%;
  position: relative;
}

.passive-product-detail-item:last-child {
  margin: 0px;
}

.passive-product-detail-item img {
  width: 100%;
}

.passive-product-detail-item .passive-product-detail-image {
  height: 90px;
}

.passive-product-detail-item.passive .passive-product-detail-image {
  position: relative;
  box-sizing: border-box;
}

.passive-product-detail-item.passive .passive-product-detail-image:after {
  content: "";
  position: absolute;
  background-color: rgba(255, 255, 255, 0.5);
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}

.passive-product-detail-item.active .passive-product-detail-image {
  border: 2px solid #ffffff;
  box-sizing: border-box;
}

.geralateerde-producten-items {
  width: 100%;
}

.geralateerde-producten-items .hot-item {
  margin-right: 1%;
  width: 24.25%;
  height: auto;
}

.geralateerde-producten-items .hot-item:last-child {
  margin: 0;
}
